<template>
<div>
  <div v-if="editData.name == null">
    <v-card>
        <v-card-text>
          <p style="text-align:center; font-size: 35px;">Belum ada data pasien</p>        
        </v-card-text>
    </v-card>
  </div>
  <div v-else>
    <v-container
    fill-height
    fluid
    grid-list-xl>
    <v-layout
      justify-center
      wrap
    >
      <v-flex
        xs12
        md8
      >
        <v-card
          color='#ffff'
          title='User Profile'
        >
          <v-form>
            <v-container py-0>
              <v-layout wrap>
               <v-flex xs12 sm6>
                  <v-text-field
                    prepend-inner-icon="perm_contact_calendar"
                    v-model="editData.nik"
                    disabled
                    label="NIK"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 sm6>
                  <v-text-field
                    prepend-inner-icon="keyboard_arrow_down"
                    v-model="editData.placeOfBirth"
                    disabled
                    label="Tempat Lahir"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    prepend-inner-icon="text_rotation_none"
                    v-model="editData.name"
                    disabled
                    label="Nama"
                  ></v-text-field>
                </v-flex>
                    <v-flex xs12>
                        Tanggal Lahir
                      </v-flex>
                      <v-flex xs4>
                          <v-text-field
                            v-model="date"
                            label="Tanggal"
                            disabled
                        ></v-text-field>
                      </v-flex>
                      <v-flex xs4>
                        <v-text-field
                            v-model="month"
                            label="Bulan"
                            disabled
                        ></v-text-field>
                      </v-flex>
                      <v-flex xs4>
                        <v-text-field
                            v-model="year"
                            label="Tahun"
                            disabled
                        ></v-text-field>                          
                      </v-flex>

                <v-flex xs12>
                  <v-text-field
                    prepend-inner-icon="room"
                    disabled
                    label="Alamat"
                    v-model="editData.address"
                  ></v-text-field>
                </v-flex>
                <v-flex xs4>
                  <v-text-field
                    prepend-inner-icon="group"
                    disabled
                    label="RT/RW"
                    v-model="editData.association"
                  ></v-text-field>
                </v-flex>
                <v-flex xs4>
                  <v-text-field
                    prepend-inner-icon="contacts"
                    disabled
                    label="Kel/Desa"
                    v-model="editData.village"                  
                  ></v-text-field>
                </v-flex>
                <v-flex xs4>
                  <v-text-field
                    prepend-inner-icon="contact_mail"
                    disabled
                    label="Kecamatan"
                    v-model="editData.subdistrict"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 sm6>
                  <v-text-field
                    prepend-inner-icon="contact_mail"
                    disabled
                    label="Agama"
                    v-model="editData.religion"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 sm6>
                <v-text-field
                  prepend-inner-icon="nature_people"
                  label="Status Kawin"
                  v-model="editData.maritalStatus"
                  disabled
                ></v-text-field>
              </v-flex>
                <v-flex xs6>
              <v-text-field
                    prepend-inner-icon="bookmarks"
                    disabled
                    label="Pekerjaan"
                    v-model="editData.job"
                  ></v-text-field>
              </v-flex>
                <v-flex xs6>
                <v-text-field
                  prepend-inner-icon="landscape"
                  label="Kependudukan"
                  v-model="editData.citizenship"
                  disabled
                ></v-text-field>
              </v-flex>
                <v-flex xs6>
                <v-text-field
                  prepend-inner-icon="account"
                  label="Jenis Kelamin"
                  v-model="editData.gender"
                  disabled
                ></v-text-field>
              </v-flex>
                <v-flex xs6>
                <v-text-field
                  prepend-inner-icon="landscape"
                  label="Golongan Darah"
                  v-model="editData.bloodType"
                  disabled
                ></v-text-field>
              </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card>
      </v-flex>
      <v-flex
        xs12
        md4
      >
        <v-flex class='v-card-profile'>
            <div class="card-text">
              <v-btn color="green" style="width: 100%" dark v-if="diagnosis.length != 0" @click.prevent="afterDiagnosisD = true">Selesai</v-btn>
            </div>
          <v-flex xs12>

                  <div class="card">
                    <div class="card-content">
                      <div class="columns">
                        <div class="column">
                          <p><strong>Diagnosis</strong></p>
                        </div>
                        <div class="column" style="text-align:right">
                          <v-btn slot="activator" fab small  dark color="indigo" @click.prevent="inputType = 'new'; inputCat = 'diagnosis'; diagnosisDialog = true">
                          <v-icon dark >add</v-icon>
                          </v-btn>
                        </div>
                      </div>
                      <div class="limitBox">
                        <div class="outsideBox" v-for="medi in diagnosis" :key="medi.id">
                            <div class="box-ticket" style="border-left: solid 5px red" >
                                <p style="color: #888888">Diagnosis <br> <strong>{{medi.name}}</strong></p>
                            </div>
                            <p class="editTool" style="text-align: right;">
                                <v-icon class="deleteButton" @click.prevent="deleteDialog = true; typeCat = 'diagnosis';deleteId = medi.id">delete</v-icon>
                                    </p>
                                </div>
                              </div>
                            </div>
                          </div>
                          </v-flex>
          <!-- <v-flex xs12>

                  <div class="card">
                    <div class="card-content">
                      <div class="columns">
                        <div class="column">
                          <p><strong>Resep Obat</strong></p>
                        </div>
                        <div class="column" style="text-align:right">
                          <v-btn slot="activator" fab small  dark color="indigo" @click.prevent="inputType = 'new'; inputCat = 'obat'; obatDialog = true;">
                          <v-icon dark >add</v-icon>
                          </v-btn>
                        </div>
                      </div>
                      <div class="limitBox">
                        <div class="outsideBox" v-for="medi in medicines" :key="medi.id">
                            <div class="box-ticket" style="border-left: solid 5px red" >
                                <p style="color: #888888">Resep Obat <br> <strong>{{medi.name}} ({{medi.total}})</strong></p>
                            </div>
                            <p class="editTool" style="text-align: right;">
                                <v-icon class="deleteButton" @click.prevent="deleteDialog = true; typeCat = 'obat';deleteId = medi.id">delete</v-icon>
                                    </p>
                                </div>
                              </div>
                            </div>
                          </div>
                          </v-flex> -->
          <v-flex xs12>
                  <div class="card">
                    <div class="card-content">
                      <div class="columns">
                        <div class="column">
                          <p><strong>Saran</strong></p>
                        </div>
                        <div class="column" style="text-align:right">
                          <v-btn slot="activator" fab small  dark color="indigo" @click.prevent="inputType = 'new'; inputCat = 'saran'; dialog = true">
                          <v-icon dark >add</v-icon>
                          </v-btn>
                        </div>
                      </div>
                      <div class="limitBox">
                        <div class="outsideBox" v-for="saran in sarans" :key="saran.id">
                            <div class="box-ticket" style="border-left: solid 5px red" >
                                <p style="color: #888888">Saran <br> <strong>{{saran.name}}</strong></p>
                            </div>
                            <p class="editTool" style="text-align: right;">
                                <v-icon class="deleteButton" @click.prevent="deleteDialog = true; typeCat = 'saran';deleteId = saran.id;">delete</v-icon>
                                    </p>
                                </div>
                              </div>
                            </div>
                          </div>
                          </v-flex>
                          <v-flex xs12>
                          <div class="card">
                            <div class="card-content">
                              <div class="columns">
                                <div class="column">
                                  <p><strong>Gejala Penyakit</strong></p>
                                </div>
                              </div>
                              <div class="limitBox">
                                <div class="outsideBox" v-for="gejala in gejalas" :key="gejala.id">
                                    <div class="box-ticket" style="border-left: solid 5px red" >
                                        <p style="color: #888888">Gejala <br> <strong>{{gejala.symptoms.name}}</strong></p> <br>
                                        <p style="margin-left: auto;margin-right: 10px; text-align: right;color: #888888">Tanggal Gejala<br> <strong>{{gejala.created_at}}</strong></p>
                                    </div>
                                        </div>
                                      </div>
                                    </div>
                                  </div>
                          </v-flex>
                        </v-flex>
                      </v-flex>
                    </v-layout>
              <v-dialog v-model="dialog" persistent max-width="600px">
                <v-form
                    ref="form"
                    v-model="valid"
                    lazy-validation
                >
              <v-card xs12>
                <v-card-title>
                   <h4>Saran</h4>
                </v-card-title>
                <v-card-text>
                  <v-container grid-list-md>
                    <v-layout wrap>
                      <v-flex xs12>
                        <v-text-field label="Masukan Saran" v-model="adviceData.name" required ></v-text-field>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                 <v-btn color="red" dark @click="dialog = false">Batal</v-btn>
                 <v-btn color="indigo" dark @click="sendData()">Sarankan</v-btn>
                </v-card-actions>
              </v-card>
                </v-form>
            </v-dialog>
              <v-dialog v-model="diagnosisDialog" persistent max-width="600px">
                <v-form
                    ref="form2"
                    v-model="valid"
                    lazy-validation
                >
              <v-card xs12>
                <v-card-title>
                   <h4>Diagnosis</h4>
                </v-card-title>
                <v-card-text>
                  <v-container grid-list-md>
                    <v-layout wrap>
                      <v-flex xs12>
                        <v-text-field label="Diagnosis" v-model="diagnosisData.name" required ></v-text-field>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                 <v-btn color="red" dark @click="diagnosisDialog = false">Batal</v-btn>
                 <v-btn color="green" dark @click="sendDataDiagnosis()">Diagnosis</v-btn>
                </v-card-actions>
              </v-card>
                </v-form>
            </v-dialog>
            <v-dialog v-model="obatDialog" persistent max-width="600px">
                <v-form
                    ref="form3"
                    v-model="valid"
                    lazy-validation
                >
              <v-card xs12>
                <v-card-title>
                   <h4>Resep Obat</h4>
                </v-card-title>
                <v-card-text>
                  <v-container grid-list-md>
                    <v-layout wrap>
                      <v-flex xs12>
                        <v-autocomplete
                  prepend-inner-icon="nature_people"
                  :items="listObat"
                  item-value="id"
                  item-text="view"
                  label="List Obat"
                  v-model="obatData.medicine_id"
                ></v-autocomplete>
                      </v-flex>
                      <v-flex xs12>
                        <v-text-field label="Jumlah Obat" v-model="obatData.total" required ></v-text-field>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                 <v-btn color="red" dark @click="obatDialog = false">Batal</v-btn>
                 <v-btn color="green" dark @click="sendDataObat()">Ajukan</v-btn>
                </v-card-actions>
              </v-card>
                </v-form>
            </v-dialog>
                    <!-- delete -->
          <v-layout row justify-center>
                  <v-dialog v-model="deleteDialog" persistent max-width="290">
                    <v-card>
                      <v-card-title class="headline">Hapus Data ?</v-card-title>
                      <v-card-text>Data yang dihapus juga akan menghapus seluruh data didalamnya dan tidak dapat dikembalikan lagi.</v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="green darken-1" dark @click="resetData()">Batal</v-btn>
                        <v-btn color="red darken-1" dark :loading="load" @click="deleteData()">Hapus</v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-layout>
        <!-- delete -->
                    <!-- afterDiagnosis -->
          <v-layout row justify-center>
                  <v-dialog v-model="afterDiagnosisD" persistent max-width="290">
                    <v-card>
                      <v-card-title class="headline">Terim kasih Dokter</v-card-title>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="green darken-1" dark @click="afterDiagnosisD = false">Kembali</v-btn>
                        <v-btn color="red darken-1" dark :loading="load" @click="afterDiagnosis()">OK</v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-layout>
        <!-- afterDiagnosis -->
        </v-container>
        <v-container
          fill-height
          fluid
          grid-list-xl>
          <v-layout
            justify-center
            wrap
          >
            <v-flex
              xs12
            >
          <div class="contentlist">
          <div class="flex-container" >
      <nav class="level">
             <div class="level-left">
                <b-field grouped group-multiline>
                    <b-select v-model="perPage" :disabled="!isPaginated">
                        <option value="10">10 per page</option>
                        <option value="20">20 per page</option>
                        <option value="50">50 per page</option>
                    </b-select>
                </b-field>
             </div>
           </nav>
        <b-table :data="histories" :paginated="true" :per-page="perPage" :current-page.sync="currentPage" :loading="loadData" :pagination-simple="true" :narrowed="true" :mobile-cards="true" :striped="true" :hoverable="true" :default-sort-direction="defaultSortDirection" default-sort="updated_at">
            <template slot-scope="props">
                <b-table-column label="No." sortable>{{ props.index + 1 }}</b-table-column>
                <b-table-column field="patien" label="Pasien" sortable>{{ props.row.user.name }}</b-table-column>
                <b-table-column field="doctor" label="Dokter" sortable>{{ props.row.doctor.name }}</b-table-column>
                <b-table-column field="diagnosis" label="Diagnosis" sortable>
                  <v-flex v-for="diag in props.row.diagnosis" :key="diag.id">  
                    <v-chip color="indigo" dark>{{diag.name}}</v-chip>
                  </v-flex>
                </b-table-column>
            </template>
              <template slot="empty">
                <section class="section">
                    <div class="content has-text-grey has-text-centered">
                        <p>
                            <b-icon
                                icon="emoticon-sad"
                                size="is-large">
                            </b-icon>
                        </p>
                        <p>Belum ada data riwayat pasien</p>
                    </div>
                </section>
            </template>
        </b-table>
      
          <v-snackbar
              v-model="snackbar"
              :color="color"
              :multi-line="true"
              :timeout="3000"
            >
              {{ text }}
              <v-btn
                dark
                flat
                @click="snackbar = false"
              >
                Close
              </v-btn>
            </v-snackbar>
            </div>
          </div>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      medicines: [],
      obatDialog: false,
      obatData: {},
      diagnosis: [],
      diagnosisData: {},
      diagnosisDialog: false,
      gejalas: [],
      sarans: [],
      dialog: false,
      valid: true,
      load: false,
      btnSaran: false,
      date: 1,
      month: 1,
      year: 1998,
      listMonth: [
        {number: 1, text: 'Januari'},
        {number: 2, text: 'Februari'},
        {number: 3, text: 'Maret'},
        {number: 4, text: 'April'},
        {number: 5, text: 'Mei'},
        {number: 6, text: 'Juni'},
        {number: 7, text: 'July'},
        {number: 8, text: 'Agustus'},
        {number: 9, text: 'September'},
        {number: 10, text: 'Oktober'},
        {number: 11, text: 'November'},
        {number: 12, text: 'Desember'}
      ],
      typeInput: 'new',
      stat: '',
      search: '',
      table: 0,
      users: [],
      deleteDialog: false,
      editDialog: false,
      snackbar: false,
      text: '',
      color: null,
      reset: false,
      editData: {
        name: null
      },
      deleteId: -1,
      loading: false,
      loadData: true,
      isPaginated: true,
      isPaginationSimple: true,
      defaultSortDirection: 'desc',
      currentPage: 1,
      perPage: 10,
      patiens: [],
      detailData: false,
      adviceData: {},
      histories: [],
      typeCat: 'saran',
      afterDiagnosisD: false,
      listObat: [],
      orderId: -1
    }
  },
  computed: {
    listYear () {
      var year = []
      var today = new Date()
      var thisYear = today.getFullYear()
      var limitAge = 5
      var startYear = thisYear - limitAge
      for (let i = 0; i < 20; i++) {
        year.push(startYear - i)
      }
      return year
    },
    listDate () {
      var date = []
      for (let i = 1; i < 32; i++) {
        date.push(i)
      }
      return date
    }
  },
  mounted () {
    this.getData()
    this.getObat()
  },
  methods: {
    getObat () {
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      uri = this.$apiUrl + 'listobat'
      this.$http.get(uri, config).then(response => {
        this.listObat = response.data
        for (let i = 0; i < this.listObat.length; i++) {
          this.listObat[i].view = this.listObat[i].type + ' ' + this.listObat[i].name
        }
      }).catch(error => {
        console.log(error)
        this.snackbar = true
        this.text = 'Coba lagi'
        this.color = 'error'
        this.load = false
      })
    },
    afterDiagnosis () {
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      uri = this.$apiUrl + 'afterdiagnosis/' + this.editData.id
      this.$http.get(uri, config).then(response => {
        this.editData.name = null
        this.afterDiagnosisD = false
        this.getData()
      }).catch(error => {
        console.log(error)
        this.snackbar = true
        this.text = 'Coba lagi'
        this.color = 'error'
        this.load = false
      })
    },
    getDataGejala (id) {
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      uri = this.$apiUrl + 'listSymptoms/' + id
      this.$http.get(uri, config).then(response => {
        this.gejalas = response.data
      }).catch(error => {
        console.log(error)
        this.snackbar = true
        this.text = 'Coba lagi'
        this.color = 'error'
        this.load = false
      })
    },
    sendDataDiagnosis () {
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      this.diagnosisData.queue_id = this.editData.id
      uri = this.$apiUrl + 'diagnosis'
      this.$http.post(uri, this.diagnosisData, config).then(response => {
        console.log(response)
        this.dialog = false
        this.getData()
      }).catch(error => {
        console.log(error)
        this.snackbar = true
        this.text = 'Coba lagi'
        this.color = 'error'
        this.load = false
      })
    },
    sendDataObat () {
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      this.obatData.patien_id = this.editData.idPatien
      uri = this.$apiUrl + 'sendMedicine'
      this.$http.post(uri, this.obatData, config).then(response => {
        console.log(response)
        this.dialog = false
        this.getData()
      }).catch(error => {
        console.log(error)
        this.snackbar = true
        this.text = 'Coba lagi'
        this.color = 'error'
        this.load = false
      })
    },
    getDataObat () {
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      uri = this.$apiUrl + 'patienMedicine/' + this.editData.idPatien
      this.$http.get(uri, config).then(response => {
        console.log(response)
      }).catch(error => {
        console.log(error)
        this.load = false
      })
    },
    sendData () {
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      this.adviceData.queue_id = this.editData.id
      uri = this.$apiUrl + 'advice'
      this.$http.post(uri, this.adviceData, config).then(response => {
        console.log(response)
        this.dialog = false
        this.getData()
      }).catch(error => {
        console.log(error)
        this.snackbar = true
        this.text = 'Coba lagi'
        this.color = 'error'
        this.load = false
      })
    },
    getData () {
      this.deleteDialog = false
      this.dialog = false
      this.load = false
      this.diagnosisDialog = false
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      uri = this.$apiUrl + 'myPatien'
      this.$http.get(uri, config).then(response => {
        if (response.data === '') {
          this.detailData = false
        } else {
          this.detailData = true
          this.seteditData(response.data)
          this.sarans = response.data.advice
          this.diagnosis = response.data.diagnosis
          this.getDataGejala(response.data.patien.id)
          this.getHistory(response.data.patien.id)
          this.getDataObat()
        }
      })
    },
    deleteData () {
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      if (this.typeCat === 'saran') {
        uri = this.$apiUrl + 'advice/' + this.deleteId
      } else {
        uri = this.$apiUrl + 'diagnosis/' + this.deleteId
      }
      this.$http.delete(uri, config).then(response => {
        this.getData()
      })
    },
    getHistory (id) {
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      uri = this.$apiUrl + 'getQueue/' + id
      this.$http.get(uri, config).then(response => {
        this.histories = response.data
        this.loadData = false
      }).catch(error => {
        console.log(error)
        this.snackbar = true
        this.text = 'Coba lagi'
        this.color = 'error'
        this.load = false
      })
    },
    seteditData (data) {
      this.editData = {
        id: data.id,
        idPatien: data.patien.id,
        name: data.patien.name,
        nik: data.patien.detail.nik,
        placeOfBirth: data.patien.detail.placeOfBirth,
        dateOfBirth: data.patien.detail.dateOfBirth,
        gender: data.patien.detail.gender,
        bloodType: data.patien.detail.bloodType,
        address: data.patien.detail.address,
        association: data.patien.detail.association,
        village: data.patien.detail.village,
        subdistrict: data.patien.detail.subdistrict,
        religion: data.patien.detail.religion,
        maritalStatus: data.patien.detail.maritalStatus,
        job: data.patien.detail.job,
        citizenship: data.patien.detail.citizenship
      }
      if (data.patien.detail.dateOfBirth !== '') {
        var d = new Date(data.patien.detail.dateOfBirth)
        this.date = d.getDate()
        this.month = parseInt(d.getMonth()) + 1
        this.year = d.getFullYear()
      }
    }
  }
}
</script>



